.services_container{
    grid-template-columns: repeat(3 , 270px);
    justify-content: center;
    column-gap: 1.8rem;
}
.services_content{
    position: relative;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: var(--container-color);
    padding: 6rem 0 2rem 2.5rem;

}
.services_icon{
    display: block;
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1);
}
.services_title{
    font-size: var(--h3-font-size);
    margin-bottom: var(--mb-1);
    font-weight: var(--font-medium);
}
.services_button{
    color: var(--title-color);
    font-size: var(--small-font-size);
    display: inline-flex;
    align-items:center;
    column-gap: 0.25rem;
    cursor: pointer;
}
.services_button_icon{
    font-size: 1rem;
    transition: 0.3s;
}
.services_button:hover , .services_button_icon{
    transform: translateX(00.25rem);
    
}

.services_modal{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: var(--z-modal);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
}
.services_modal_content{
    width: 500px;
    position: relative;
    background-color: var(--container-color);
    padding: 4.5rem 2.5rem 2.5rem;
    border-radius: 1.5rem;

}
.services_modal_close{
    position: absolute;
    top: 1.5rem;
    right: 1.5rem;
    font-size: 1.5rem;
    color: var(--title-color);
    cursor: pointer;
}
.services_modal_title , .services_modal_description{
    text-align: center;
}
.services_modal_title{
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1);

}
.services_modal_description{
    font-size: var(--small-font-size);
    padding: 0 3.5rem;
    margin-bottom: var(--mb-2);
}
.services_modal_service{
    row-gap: 0.75rem;
}
.services_modal_service{
    display: flex;
    align-items: center;
    column-gap: .5rem;
}
.services_modal_icon{
    color: var(--title-color);
    font-size: 1.1rem;

}
.services_modal_info{
    font-size: var(--small-font-size);

}
.active_modal{
    opacity: 1;
    visibility: visible;
}

/* Breakpoints */
/* For Large Devices */
@media screen and (max-width :992px){
    .services_container{
        grid-template-columns: repeat(3,218px);
    }
  }
  
  /* For Meadium Devices */
  @media screen and (max-width :768px){
    .services_container{
        grid-template-columns: repeat(2, 1fr);
    }
    .services_content{
        padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    }
    .services_icon{
        font-size: 1.5rem;
    }
  }
  
  @media screen and (max-width :576px){
    .services_modal_content{
        padding: 4.5rem 1.5rem 2rem;
    }
    .services_modal_description{
        padding: 0;
    }
  }
  
  
  /* For Small Devices */
  @media screen and (max-width :350px){
    .services_container{
        grid-template-columns: max-content;
    }
    .services_content{
        padding: 6rem;
    }
  }
  
  